.companions {
  background: url("../img/background-square-dark.svg") no-repeat top 34px left -19px, url("../img/background-image-smolov.png") no-repeat top 35px right 25px, url("../img/background-image-rogovaya.png") no-repeat top 307px left @background-deep-blue;
  background-size: 132px 132px, 143px 143px, 62px 71px;
  padding: 68px 25px 31px;
  border-radius: 0 0 30px 30px;
  position: relative;

  &::before {
    display: block;
    content: "";
    width: 100%;
    height: 50px;
    position: absolute;
    background-color: @background-deep-blue;
    top: -50px;
    left: 0;
    z-index: -1;
  }

  @media (min-width: @tablet-width) {
    padding: 142px 74px 87px;
    border-radius: 0 0 60px 60px;
    background: url("../img/background-square-dark.svg") no-repeat top 71px left 40px, url("../img/background-image-rogovaya--tablet.png") no-repeat top 80px right 74px, url("../img/background-image-smolov.png") no-repeat top 426px left 40px @background-deep-blue;
    background-size: 212px 212px, 285px 285px, 143px 143px;

    @media (min-resolution: 2dppx) {
      background: url("../img/background-square-dark.svg") no-repeat top 71px left 40px, url("../img/background-image-rogovaya--tablet@2x.png") no-repeat top 80px right 74px, url("../img/background-image-smolov@2x.png") no-repeat top 426px left 40px @background-deep-blue;
      background-size: 212px 212px, 285px 285px, 143px 143px;
    }
  }

  @media (min-width: @desktop-width) {
    padding: 130px 60px 134px 129px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: min-content min-content min-content min-content;
    background: url("../img/background-square-dark.svg") no-repeat top 70px left 60px, url("../img/background-image-rogovaya--tablet.png") no-repeat bottom 80px left 190px, url("../img/background-image-smolov.png") no-repeat top 134px left 42% @background-deep-blue;
    background-size: 212px 212px, 285px 285px, 143px 143px;

    @media (min-resolution: 2dppx) {
      background: url("../img/background-square-dark.svg") no-repeat top 70px left 60px, url("../img/background-image-rogovaya--tablet@2x.png") no-repeat bottom 80px left 190px, url("../img/background-image-smolov@2x.png") no-repeat top 134px left 42% @background-deep-blue;
      background-size: 212px 212px, 285px 285px, 143px 143px;
    }
  }
}

.companions__list {
  @media (min-width: @desktop-width) {
    grid-column: -1 / -2;
    grid-row: -1 / 1;
  }
}

.companions__title {
  color: @special-orange;
  margin: 0 0 0 24px;
  font-weight: 700;
  font-size: 24px;
  line-height: 1em;

  @media (min-width: @tablet-width) {
    font-size: 45px;
    margin: 0;
  }
}

.companions__about {
  color: @basic-white;
  font-weight: 300;
  line-height: 22px;
  padding: 19px 29px 20px 24px;
  margin: 0;

  @media (min-width: @tablet-width) {
    font-size: 28px;
    line-height: 34px;
    padding: 55px 185px 39px 0;
  }

  @media (min-width: @desktop-width) {
    padding: 12px 150px 0 0;
  }
}

.companions__about-brake {
  display: none;

  @media (min-width: @tablet-width) {
    display: inline;
  }
}

.companions__about--appeal {
  font-weight: 700;
  padding: 0 49px 54px 24px;
  margin: 0;

  @media (min-width: @tablet-width) {
    font-size: 21px;
    line-height: 24px;
    padding: 0 350px 59px 0;
  }

  @media (min-width: @desktop-width) {
    padding: 0 200px 0 0;
  }
}

.companions .companions__show-more {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-transform: none;
  text-align: start;
  font-size: 18px;
  line-height: 1em;
  border-radius: 10px;
  padding: 22px 23px 21px;

  &:hover {
    background-color: @special-yellow-dark;
  }

  &:active {
    color: rgba(29, 46, 91, 0.3);

    &::after {
      border-left-color: rgba(29, 46, 91, 0.3);
    }
  }

  &::after {
    display: block;
    content: "";
    width: 0;
    height: 0;
    border-top: 7px solid transparent;
    border-left: 11px solid @ocean-blue;
    border-bottom: 7px solid transparent;
  }

  @media (min-width: @tablet-width) {
    font-size: 24px;
    line-height: 1em;
    padding: 28px 33px;
    border-radius: 20px;
  }

  @media (min-width: @desktop-width) {
    max-width: 214px;
    height: min-content;
    align-self: end;
  }
}
