.companion-flags {
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  position: absolute;
  top: -17px;

  @media (min-width: @tablet-width) {
    top: -12px;
  }
}

.companion-flags__item {
  .companion-transport__item;
  width: 35px;
  position: relative;

  &:hover {
    &::before {
      display: block;
      padding: 18px 25px;
      width: max-content;
      border-radius: 4px;
      background-color: #101d41;
      position: absolute;
      top: 36px;
      bottom: unset;
    }

    &::after {
      top: 23px;
    }
  }

  &:not(:last-child) {
    margin-right: 20px;
  }
}

.companion-flags__item--shri-lanka {
  &:hover {
    &::before {
      content: "-";
    }
  }
}

.companion-flags__item--thailand {
  &:hover {
    &::before {
      content: "";
    }
  }
}

.companion-flags__item--seychelles {
  &:hover {
    &::before {
      content: "";
    }
  }
}

.companion-flags__item--belgium {
  &:hover {
    &::before {
      content: "";
    }
  }
}

.companion-flags__item--czech {
  &:hover {
    &::before {
      content: "";
    }
  }
}
