.page {
  height: 100%;
}
.page .page__main-list {
  display: none;
  margin: 0;
}
@media (min-width: 1440px) {
  .page .page__main-list {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
  }
}
.page__main-list .page-list__link {
  color: #ffffff;
}
.page__body {
  display: flex;
  flex-direction: column;
  background-color: #e6e8f1;
  font-family: "Blogger Sans", "Arial", sans-serif;
  font-size: 16px;
  line-height: 18px;
  margin: 0 auto;
}
.page__body--catalog {
  background-color: #e5e5e5;
}
.page__menu-wrapper {
  display: flex;
}
@media (min-width: 768px) {
  .page__menu-wrapper {
    order: -2;
  }
}
@media (min-width: 1440px) {
  .page__menu-wrapper {
    order: -3;
  }
}
.page__title {
  font-weight: 700;
  font-size: 36px;
  line-height: 36px;
  color: #ffffff;
  margin: 0;
  width: max-content;
  position: relative;
}
.page__title::after {
  content: "";
  position: absolute;
  right: 0;
  bottom: -13px;
  left: 0;
  height: 5px;
  border-radius: 4px;
  background: #ff8d30;
  z-index: 3;
}
@media (min-width: 768px) {
  .page__title {
    font-size: 60px;
    line-height: 1em;
  }
  .page__title::after {
    bottom: -70px;
    height: 8px;
  }
}
.page__main-logo {
  background: url("../img/logo-light.svg") no-repeat;
  width: 96px;
  height: 15px;
}
@media (min-width: 768px) {
  .page__main-logo {
    background: url("../img/logo-full-light.svg") no-repeat;
    width: 166px;
    height: 42px;
  }
}
@media (min-width: 1440px) {
  .page__main-toggle {
    display: none;
  }
}
.main-header {
  background-color: #182044;
  border-radius: 0 0 20px 20px;
  z-index: 3;
}
@media (min-width: 768px) {
  .main-header {
    border-radius: 0 0 60px 60px;
    background-color: #182044;
  }
}
@media (min-width: 1440px) {
  .main-header {
    display: grid;
  }
}
.main-header--main-page {
  background-color: #161c35;
  border-radius: 0 0 30px 30px;
  z-index: 6;
}
@media (min-width: 768px) {
  .main-header--main-page {
    background-color: #182044;
    border-radius: 0 0 60px 60px;
  }
}
.main-header__wrapper {
  padding: 96px 25px 11px;
}
@media (min-width: 768px) {
  .main-header__wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 225px 75px 65px;
  }
}
@media (min-width: 1440px) {
  .main-header__wrapper {
    grid-template-columns: min-content;
    padding: 225px 124px 65px;
  }
}
.main-header__wrapper .main-header__contacts {
  display: none;
}
@media (min-width: 768px) {
  .main-header__wrapper .main-header__contacts {
    display: flex;
  }
}
.main-header .main-header__list {
  display: none;
}
@media (min-width: 1440px) {
  .main-header .main-header__list {
    display: flex;
    justify-content: space-between;
  }
}
@media (min-width: 1440px) {
  .main-header__list .page-list__link {
    color: #ffffff;
  }
}
.main-header__logo {
  width: min-content;
}
@media (min-width: 1440px) {
  .main-header__logo {
    order: -2;
    height: 50px;
  }
}
.main-header__logo picture:nth-child(1) {
  display: block;
}
.main-header__logo picture:nth-child(2) {
  display: none;
}
.main-header__logo--active picture:nth-child(1) {
  display: none;
}
.main-header__logo--active picture:nth-child(2) {
  display: block;
}
.main-header__logo--active-scrolled picture:nth-child(1) {
  display: block;
}
.main-header__logo--active-scrolled picture:nth-child(2) {
  display: none;
}
.main-header__return-button {
  display: none;
  font-weight: 700;
  font-size: 20px;
  line-height: 20px;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  justify-self: end;
  align-self: center;
}
.main-header__return-button:hover {
  opacity: 0.6;
}
.main-header__return-button:active {
  opacity: 0.3;
}
.main-header__return-button::after {
  display: block;
  content: "";
  width: 32px;
  height: 16px;
  margin-left: 28px;
  background: url("../img/icon-arrow-round.svg") no-repeat center;
}
@media (min-width: 768px) {
  .main-header__return-button {
    display: flex;
    order: 1;
  }
}
@media (min-width: 1440px) {
  .main-header__return-button {
    grid-column: 4 / 5;
  }
}
.main-header .main-nav--scrolled {
  background-color: #ffffff;
  padding: 27px 25px;
}
@media (min-width: 768px) {
  .main-header .main-nav--scrolled {
    padding: 45px 40px;
  }
}
@media (min-width: 1440px) {
  .main-header .main-nav--scrolled {
    padding: 20px 60px;
  }
}
.page-list {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  padding: 0;
  align-items: center;
  grid-column: 2 / -2;
}
@media (min-width: 768px) {
  .page-list {
    align-items: start;
  }
}
@media (min-width: 1440px) {
  .page-list {
    order: -2;
    flex-direction: row;
  }
  .page-list::before {
    display: block;
    content: "";
    width: 60px;
    height: 2px;
    background-color: #ffffff;
    margin-right: 40px;
  }
}
.page-list__item:not(:last-child) {
  margin-bottom: 5px;
}
@media (min-width: 768px) {
  .page-list__item:not(:last-child) {
    margin: 0 33px 11px 0;
  }
}
@media (min-width: 1440px) {
  .page-list__item:not(:last-child) {
    margin: 0 24px 0 0;
  }
}
.page-list__link {
  display: block;
  color: #192144;
  font-size: 30px;
  line-height: 35px;
  font-weight: 500;
  text-decoration: none;
  text-transform: uppercase;
  position: relative;
  transition: transform 0.3s;
  box-sizing: border-box;
  overflow: hidden;
  height: 1em;
  padding: 0 6px;
}
.page-list__link:hover {
  outline: none;
}
@media (min-width: 1440px) {
  .page-list__link:hover {
    color: #ffd74b;
  }
}
@media (min-width: 1440px) {
  .page-list__link:hover span {
    transform: translateY(-100%);
  }
}
@media (min-width: 1440px) {
  .page-list__link:active {
    color: rgba(255, 215, 75, 0.3);
  }
}
@media (min-width: 768px) {
  .page-list__link {
    font-size: 60px;
    line-height: 71px;
    padding: 0 10px 0 0;
  }
}
@media (min-width: 1440px) {
  .page-list__link {
    color: #ffffff;
    font-size: 20px;
    line-height: 20px;
    padding: 0 5px 0 5px;
    text-align: center;
  }
}
.page-list__link span {
  position: relative;
  display: inline-block;
  transition: transform 0.3s;
  box-sizing: border-box;
}
.page-list__link span::before {
  position: absolute;
  top: 100%;
  content: attr(data-hover);
  font-weight: 700;
  transform: translate3d(0, 0, 0);
  box-sizing: border-box;
  width: max-content;
}
.page-list__link--current {
  transition: none;
  cursor: default;
}
@media (min-width: 1440px) {
  .page-list__link--current:hover {
    color: #ffffff;
  }
}
.page-list__link--current:hover span {
  transform: translateY(0%);
}
.page-list__link--current span:hover {
  transform: none;
}
.main-container {
  flex-grow: 1;
}
.contacts-list {
  padding: 0;
  margin: 0 0 28px;
  list-style-type: none;
}
@media (min-width: 1440px) {
  .contacts-list {
    display: flex;
  }
}
.contacts-list__item {
  font-size: 16px;
  line-height: 18px;
  font-weight: 700;
  text-transform: uppercase;
  align-items: center;
}
@media (min-width: 768px) {
  .contacts-list__item {
    font-size: 28px;
    line-height: 1em;
    column-gap: 21px;
  }
}
@media (min-width: 1440px) {
  .contacts-list__item {
    column-gap: 0;
  }
}
.contacts-list__link {
  display: flex;
  justify-content: space-between;
  content: "";
  width: 45px;
  height: 45px;
  border-radius: 50%;
  background-color: #121936;
  position: relative;
}
.contacts-list__link::after {
  display: block;
  width: max-content;
  height: auto;
  top: calc(50% - 9px);
  left: calc(100% + 16px);
  position: absolute;
  color: #192144;
}
@media (min-width: 768px) {
  .contacts-list__link::after {
    top: calc(50% - 14px);
    left: calc(100% + 23px);
  }
}
@media (min-width: 768px) {
  .contacts-list__link {
    width: 68px;
    height: 68px;
    background-size: 27px 26px;
  }
}
@media (min-width: 1440px) {
  .contacts-list__link {
    width: 50px;
    height: 50px;
    margin: 0;
  }
  .contacts-list__link::before {
    display: none;
  }
}
.contacts-list__phone {
  background-image: url("../img/icons-mini/icons-yellow/icon-telephone-yellow.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.contacts-list__phone::after {
  content: "8 800 555-86-28";
}
.contacts-list__email {
  background-image: url("../img/icons-mini/icons-yellow/icon-mail-yellow.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.contacts-list__email::after {
  content: "mail@htmlacademy.ru";
}
@media (min-width: 1440px) {
  .contacts-list__phone--simple {
    background: url("../img/icons-mini/icons-dark/icon-phone-dark.svg") no-repeat center #ff8d30;
  }
  .contacts-list__phone--simple:hover {
    background-color: #f26926;
  }
  .contacts-list__phone--simple:active {
    background: url("../img/icons-mini/icons-dark/icon-phone-dark--active.svg") no-repeat center #f26926;
  }
}
@media (min-width: 1440px) {
  .contacts-list__email--simple {
    background: url("../img/icons-mini/icons-dark/icon-envelope-dark.svg") no-repeat center #ffbd30;
  }
  .contacts-list__email--simple:hover {
    background-color: #ff8d30;
  }
  .contacts-list__email--simple:active {
    background: url("../img/icons-mini/icons-dark/icon-envelope-dark--active.svg") no-repeat center #ff8d30;
  }
}
@media (min-width: 1440px) {
  .contacts-list__phone::after,
  .contacts-list__email::after {
    content: "";
  }
}
.contacts-list__text--dark {
  color: #192144;
}
@media (min-width: 1440px) {
  .contacts-list__text--dark {
    display: none;
  }
}
.contacts-list__link--registration {
  font-weight: 700;
  line-height: 22px;
  display: flex;
  align-items: center;
}
.contacts-list__link--registration::after {
  color: #ffd74b;
  font-size: 16px;
  line-height: 22px;
}
@media (min-width: 1440px) {
  .contacts-list__link--registration {
    margin-right: 31px;
    min-width: 90px;
    height: 90px;
  }
}
.social-media {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  max-width: 100%;
}
.social-media__item {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 45px;
  height: 45px;
}
@media (min-width: 768px) {
  .social-media__item {
    width: 62px;
    height: 62px;
  }
}
.social-media__link {
  display: flex;
  align-items: center;
  justify-content: center;
}
.social-media__footer-link {
  background-color: #ffd74b;
  width: 45px;
  height: 45px;
  border-radius: 50%;
}
.social-media__footer-link:hover {
  background-color: #ffbd30;
}
.social-media__footer-link:active svg {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .social-media__footer-link {
    min-width: 62px;
    height: 62px;
  }
}
.main-nav {
  width: calc(100vw - 50px);
  display: grid;
  grid-template-columns: 1fr;
  background-color: #182044;
  padding: 19px 25px 27px;
  position: absolute;
  z-index: 4;
}
@media (min-width: 768px) {
  .main-nav {
    width: calc(100vw - 80px);
    background-color: #182044;
    padding: 45px 40px;
  }
}
@media (min-width: 1440px) {
  .main-nav {
    width: calc(100vw - 120px);
    grid-template-columns: min-content 1fr 1fr 1fr min-content;
    padding: 47px 60px 0;
    position: absolute;
  }
}
.main-nav--sticky {
  position: fixed;
}
@media (min-width: 1440px) {
  .main-nav--sticky {
    padding: 47px 60px;
  }
}
@media (max-width: 1439px) {
  .main-nav--scrolled {
    background-color: #ffffff;
  }
}
.main-nav--main-page {
  background-color: #161d36;
}
@media (min-width: 768px) {
  .main-nav--main-page {
    background-color: #182044;
  }
}
@media (min-width: 1440px) {
  .main-nav--main-page {
    background-color: #161d36;
  }
}
.main-nav--active {
  background-color: #ffffff;
  padding: 27px 25px;
}
@media (min-width: 768px) {
  .main-nav--active {
    padding: 45px 40px;
  }
}
@media (min-width: 1440px) {
  .main-nav--active {
    background-color: #161d36;
    padding: 47px 60px 0;
  }
}
.main-nav__wrapper {
  display: flex;
  flex-direction: column;
}
.main-nav__list {
  margin: 71px 0 0 0;
}
@media (min-width: 768px) {
  .main-nav__list {
    grid-column: -1 / 1;
    margin: 93px 57px 140px;
  }
}
@media (min-width: 1440px) {
  .main-nav__list {
    grid-column: 2 / -3;
    flex-direction: row;
    align-items: center;
    margin: 7px 0 0 38px;
  }
}
@media (min-width: 1440px) {
  .main-nav__list--active::before {
    background-color: #192144;
  }
}
@media (min-width: 1440px) {
  .main-nav__list--active .page-list__link {
    color: #192144;
  }
}
@media (min-width: 1440px) {
  .main-nav .contacts-list {
    order: -1;
    grid-column: -2 / -3;
    justify-content: end;
    margin: 0;
    display: flex;
  }
}
.main-nav .main-nav__login {
  padding: 12px 0 8px;
  margin: 56px 0 52px;
  line-height: 30px;
}
.main-nav .main-nav__login:hover {
  background-color: #ffbd30;
}
.main-nav .main-nav__login:active {
  color: rgba(22, 28, 53, 0.3);
}
@media (min-width: 768px) {
  .main-nav .main-nav__login {
    position: absolute;
    top: 40px;
    right: 40px;
    padding: 16px 28px 14px 29px;
    margin: 0;
    font-size: 20px;
    line-height: 1em;
    min-width: 131px;
  }
}
@media (min-width: 1440px) {
  .main-nav .main-nav__login {
    position: static;
    order: 0;
    grid-column: -1 / -2;
    font-size: 20px;
    line-height: 20px;
  }
}
@media (min-width: 768px) {
  .main-nav .main-nav__login--active {
    font-weight: 400;
  }
}
@media (min-width: 768px) {
  .main-nav__contacts {
    justify-content: start;
    grid-column: -1 / 1;
    margin: 0 0 113px 57px;
  }
}
@media (min-width: 1440px) {
  .main-nav__contacts {
    display: none;
  }
}
.main-nav__contacts .social-media__item:not(:last-child) {
  margin-right: 27px;
}
.main-nav__toggle {
  position: absolute;
  top: 23px;
  right: 25px;
}
.main-nav__toggle::before,
.main-nav__toggle::after {
  display: block;
  content: "";
  width: 25px;
  height: 3px;
  border-radius: 1.5px;
  background-color: #ffffff;
}
.main-nav__toggle::after {
  margin-top: 5px;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    top: 60px;
    right: calc(50% - 13px);
  }
}
@media (min-width: 1440px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav__toggle--active {
  top: 33px;
}
.main-nav__toggle--active::before,
.main-nav__toggle--active::after {
  background-color: #cccccc;
}
.main-nav__toggle--active::before {
  transform: rotate(45deg);
}
.main-nav__toggle--active::after {
  margin-top: -3px;
  transform: rotate(-45deg);
}
@media (min-width: 768px) {
  .main-nav__toggle--active {
    top: 65px;
  }
}
@media (min-width: 768px) {
  .main-nav__socials {
    justify-content: start;
    margin: 0 0 27px 55px;
  }
}
@media (min-width: 1440px) {
  .main-nav__socials {
    display: none;
  }
}
.main-nav--nojs {
  background: #ffffff;
  position: static;
}
@media (min-width: 1440px) {
  .main-nav--nojs {
    background-color: inherit;
  }
}
.options {
  background-color: #edeff6;
  padding: 71px 25px 34px;
  border-radius: 0 0 30px 30px;
  position: relative;
  z-index: 5;
}
.options::before {
  display: block;
  content: "";
  width: 100%;
  height: 50px;
  position: absolute;
  background-color: #edeff6;
  top: -50px;
  left: 0;
  z-index: -1;
}
@media (min-width: 768px) {
  .options {
    padding: 73px 75px 80px;
    border-radius: 0 0 60px 60px;
    background: url("../img/background-square--tablet.svg") no-repeat top 406px left 40px, url("../img/background-flags--tablet.png") no-repeat top 80px right 40px #edeff6;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .options {
    background: url("../img/background-square--tablet.svg") no-repeat top 406px left 40px, url("../img/background-flags--tablet@2x.png") no-repeat top 80px right 40px #edeff6;
    background-size: 141px 141px, 288px 574px;
  }
}
@media (min-width: 1440px) {
  .options {
    padding: 161px 200px 138px 130px;
    display: grid;
    column-gap: 171px;
    grid-template-columns: 560px 1fr;
    grid-template-rows: min-content min-content min-content;
    background: url("../img/background-square--tablet.svg") no-repeat top 90px left 60px, url("../img/background-flags--desktop.png") no-repeat top 88px right 60px #edeff6;
  }
}
@media (min-width: 1440px) and (min-resolution: 2dppx) {
  .options {
    background: url("../img/background-square--tablet.svg") no-repeat top 90px left 60px, url("../img/background-flags--tablet@2x.png") no-repeat top 88px right 60px #edeff6;
    background-size: 140px 140px, 288px 574px;
  }
}
.options__title {
  font-weight: 700;
  font-size: 24px;
  line-height: 24px;
  color: #ff8d30;
  margin: 0 0 0 25px;
}
@media (min-width: 768px) {
  .options__title {
    font-size: 45px;
    line-height: 1em;
    margin: 0 0 57px;
  }
}
@media (min-width: 1440px) {
  .options__title {
    margin-bottom: 62px;
  }
}
.options__list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .options__list {
    align-items: start;
  }
}
@media (min-width: 1440px) {
  .options__list {
    grid-row: 1 / -1;
  }
}
.options__link {
  display: grid;
  text-decoration: none;
  width: max-content;
  position: relative;
}
.options__description {
  color: #444444;
  font-weight: 300;
  line-height: 22px;
  padding: 19px 50px 58px 24px;
  margin: 0;
}
@media (min-width: 768px) {
  .options__description {
    font-size: 28px;
    line-height: 33px;
    margin: 0 0 104px 0;
    width: 55%;
    padding: 0;
  }
}
@media (min-width: 1440px) {
  .options__description {
    margin: 0 0 96px 0;
    width: auto;
  }
}
.options .options__show-more {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-transform: none;
  text-align: start;
  height: 50px;
  font-size: 18px;
  line-height: 1em;
  border-radius: 10px;
  padding: 6px 22px;
}
.options .options__show-more:hover {
  background-color: #ffbd30;
}
.options .options__show-more:active {
  color: rgba(29, 46, 91, 0.3);
}
.options .options__show-more:active::after {
  border-left-color: rgba(29, 46, 91, 0.3);
}
.options .options__show-more::after {
  display: block;
  content: "";
  width: 0;
  height: 0;
